.todoSectionContainer{
     display: flex;
     flex-wrap: wrap;
 }
 
 .todoSectionContainer__buttons{
     background-color: transparent;
     border: none;
     color: teal;
     margin-left: 10px;
 
 }
 
 @media (max-width: 767px) {
     .todoSectionContainer__buttons {
         margin: 10px;
         flex-basis: 100%;
     }
 }
 
 .todoSectionContainer__buttons:hover {
     color: hsl(180, 100%, 35%);
 }
 
 .todoSectionContainer__buttons:active {
     color: hsl(180, 100%, 40%);
 }
 
 .todoSectionContainer__buttons--hidden{
     visibility: hidden;
 }
 
 .todoSectionContainer__buttons:disabled{
     color: #bfbfbf;
 }