

.todoTasks {
     list-style-type: none;
     padding: 0;
     margin: 0;
 }
 
 .todoTasks__item {
     display: grid;
     grid-template-columns: auto 1fr auto;
     align-items: center;
     border-bottom: 2px solid #ddd;
     padding: 10px;
     gap: 10px;
 }
 
 .todoTasks__items--hidden{
     display: none;
 }
 
 .todoTasks__item--toggleCompleted {
     text-decoration: line-through;
 }
 
 .todoTasks__button {
     display: flex;
     justify-content: center;
     align-items: center;
     background-color: green;
     color: #fff;
     border: none;
     width: 30px;
     height: 30px;
     padding: 0px;
     margin: 0;
     transition: background 0.4s;
 }
 
 .todoTasks__button:hover {
     background-color: hsl(120, 100%, 30%);
 }
 
 .todoTasks__button:active {
     background-color: hsl(120, 100%, 35%);
 }
 
 .todoTasks__button--deleted {
     background-color: rgb(250, 48, 48)
 }
 
 .todoTasks__button--deleted:hover {
     background-color: hsl(0, 100%, 65%);
 }
 
 .todoTasks__button--deleted:active {
     background-color: hsl(0, 100%, 75%);
 }